import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PanelComponent } from './panel.component';

const routes: Routes = [
  {
    path: '',
    component: PanelComponent,
    children: [
      {
        path: 'dashboard',
        loadChildren: () => import('../../pages/dashboard/dashboard.module').then(m => m.DashboardModule)
      },
      {
        path: 'empresas',
        loadChildren: () => import('../../pages/empresas/empresas.module').then(m => m.EmpresasModule)
      },
      {
        path: 'empresa/cadastro',
        loadChildren: () => import('../../pages/empresa/empresa.module').then(m => m.EmpresaModule)
      },
      {
        path: 'empresa/:id',
        loadChildren: () => import('../../pages/empresa/empresa.module').then(m => m.EmpresaModule)
      },
      {
        path: 'configuracoes',
        loadChildren: () => import('../../pages/configuracoes/configuracoes.module').then(m => m.ConfiguracoesModule)
      },
      {
        path: 'relatorios',
        loadChildren: () => import('../../pages/relatorios/relatorios.module').then(m => m.RelatoriosModule)
      },
      {
        path: '**',
        redirectTo: '/dashboard'
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PanelRoutingModule { }
